<template>
  <div class="mynes">
    <header class="count_top header">
      <img src="../../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>个人设置</span>
    </header>
    <div class="mynes-list">
      <div class="zoomBor1">
        <div>头像</div>
        <div class="mynes-left">
          <img src="../../../assets/per_default.png" alt="">
          <img src="" alt="">
          <!--<img src="../../../assets/shopImage/right.png">-->
        </div>
      </div>
      <div class="zoomBor1">
        <div>用户名</div>
        <div class="xiugaiInput" v-if="xiuAdd">
          <input placeholder="请输入用户名" type="text" v-model="user.nickname">
          <div @click="xiuName">完成</div>
        </div>
        <div class="mynes-left" v-if="!xiuAdd" @click="xiuAdd = !xiuAdd">
          <div>{{user.nickname}}</div>
          <img src="../../../assets/shopImage/right.png">
        </div>
      </div>
    </div>
    <div class="mynes-list">
      <div>
        <div>修改密码</div>
        <div @click="newShow = true" v-if="!newShow" class="mynes-left">
          <img src="../../../assets/shopImage/right.png">
        </div>
        <div v-if="newShow" class="mynes-left">
          <div @click="xiuMi" style="color: #ff6f00">完成</div>
        </div>
      </div>
    </div>
    <transition name="slide">
      <div class="mynes-list mynes-list-input" v-if="newShow">
        <div>
          <div>旧密码</div>
          <div class="xiugaiInput">
            <input placeholder="请输入旧密码" type="password" v-model="old_password">
          </div>
        </div>
        <div>
          <div>新密码</div>
          <div class="xiugaiInput">
            <input placeholder="需要8~16位,字母与数字组合" type="password" v-model="password">
          </div>
        </div>
        <div>
          <div>确认密码</div>
          <div class="xiugaiInput">
            <input placeholder="需要8~16位,字母与数字组合" type="password" v-model="repassword">
          </div>
        </div>
      </div>
    </transition>
    <div class="mynes-list">
    <div @click="bindAddress">
      <div>地址管理</div>
      <div  class="mynes-left">
        <img src="../../../assets/shopImage/right.png">
      </div>
    </div>
      <div @click="bindCard">
        <div>银行卡管理</div>
        <div  class="mynes-left">
          <img src="../../../assets/shopImage/right.png">
        </div>
    </div>
  </div>


    <div class="mynes-tuideng" @click="tui">
      退出登录
    </div>
  </div>
</template>

<script>
  import { Toast} from 'mint-ui';
  export default {
    data () {
      return {
        user:{
          name:'雪人速送',
          price:'../../static/img/snow/success.png'
        },
        xiuAdd:false,
        newShow:false,  //密码展示
        old_password:'',
        password :'',
        repassword:'',
        zhiShow:false,  //支付密码修改展示

        old_set_password:'',  //旧支付密码
        set_password:'',  //新支付密码
        set_repassword:'',  //确认支付密码
      }
    },
    methods: {
      bindAddress() {
        this.$router.push('/site')
      },
      bindCard(){
          this.$router.push('/bankList')
      },
      back() {
        this.$router.go(-1);
      },
      yanzheng:function () {

      },
      tui:function () {
        this.post('home/User/exitLogin',{}).then(res => {
          if(res.data.code === 10000){
            localStorage.clear();
            this.$router.push({
              name:'submit'
            })
          }
        })

      },

      xiuName () {
        this.xiuAdd = false;
        this.post('home/User/updateNickname',{nickname:this.user.nickname}).then(res=>{
          if (res.data.code === 10000) {
            this.xiuAdd = false
          }
        })
      },

      xiuMi() {   //修改用户密码
        if(this.old_password.length === 0 && this.password.length === 0 && this.repassword.length === 0){
          this.newShow = false;
          return
        }
        if(this.old_password.length === 0 ||this.password.length === 0 || this.repassword.length === 0){
          Toast('提示', '请输入完整密码');
          return
        }
        this.post('home/User/updatePassword',{oldpassword:this.old_password,password:this.password,password1:this.repassword}).then(
          res=>{
            if (res.data.code === 10000) {
              Toast('重置完成,请重新登录');
              this.newShow = false;
              this.tui()
            } else {
              Toast(res.data.info);
            }
          })
      },
    },
    mounted(){
      this.post('home/User/getUserInfo',{}).then(res=>{
        if (res.data.code === 10000) {
            this.user = res.data.info
        }
      })
    },
    components: {

    }
  }
</script>

<style scope>
  @import "../../../../static/header.css";
  .mynes{
    box-sizing: border-box;
    height: 100vh;
    background-color: #f2f2f2;
  }
  .mynes-list{
    padding:0 .2rem;
    box-sizing: border-box;
    background-color: #fff;
  }
  .mynes-list-input{
    margin-top: 0;
  }
  .mynes-list>div,.mynes-left,.mynes-tuideng{
    height: .9rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
  }
  .mynes-left>img:first-child{
    height: .5rem;
    width:.5rem;
    border-radius: 50%;
  }
  .mynes-left>img:last-child {
    transform: scale(.3);
    height: 100%;
    margin-left:.1rem;
  }
  .mynes-left{
    color: #777;
    width:2rem;
    display: flex;
    justify-content: flex-end;
  }
  .mynes-tuideng{
    margin-top:.7rem;
    justify-content: center;
  }
  .xiugaiInput{
    height:100%;
    display: flex;
    align-items: center;
    padding:.1rem 0;
    box-sizing: border-box;
    width:68%;
  }
  .xiugaiInput>input{
    height: 100%;
    width:100%;
    border:1px solid #ccc;
    padding:0 .1rem;
  }
  .xiugaiInput>div{
    color: #ff6f00;
    margin-left: .2rem;
    width:1.5rem;
    height:100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .slide-enter-active {
    transition: all .3s ease;
  }
  .slide-leave-active {
    transition: all .3s ease;
  }
  .slide-enter, .slide-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateY(-10px);
    opacity: 0;
  }
  .mynes input{
    border:none;
    font-size: .3rem;
  }
</style>



